<template>

    <div class='SyntaxContent'>
        <h2>Spring</h2>
    
        <!---------------------------------------------------------->
        <div class='sub-title first-title'>
            Spring 
        </div>

        <span style='margin-left:30px'>Spring  J2EE </span><br/>
        <br/><span style='margin-left:30px'> Spring   1  Spring Spring AOP IOC  IOC  Spring  Spring AOP  AOP </span>
        <br/><img style="margin-top:30px;" width="80%" src='./Spring1.png'/>


        <div class='mark-block'>
            <el-button type="primary" icon="el-icon-edit" circle @click='clickNote(1)'></el-button>
            <el-button type="success" icon="el-icon-check" circle @click='clickKnow(1)'></el-button>
            <el-button type="warning" icon="el-icon-star-off" circle @click='clickUnknow(1)'></el-button>
        </div>
        <el-divider></el-divider>


        <!---------------------------------------------------------->
        <div class='sub-title second-title'>
            Spring 
        </div>

        <span style='margin-left:30px'>Spring  7 Spring  bean </span><br/>
        
        <img style="margin-top:30px;" width="80%" src='./Spring2.png'/>
        <br/><span style='margin-left:30px'> Spring  </span><br/>


        <div class='mark-block'>
            <el-button type="primary" icon="el-icon-edit" circle @click='clickNote(2)'></el-button>
            <el-button type="success" icon="el-icon-check" circle @click='clickKnow(2)'></el-button>
            <el-button type="warning" icon="el-icon-star-off" circle @click='clickUnknow(2)'></el-button>
        </div>

        <el-divider></el-divider>
        <!---------------------------------------------------------->
        <div class='sub-title third-title'>
            IOC  AOP
        </div>

        <span style='margin-left:30px'><b></b>  Spring  IOC   </span><br/>
        <br/><span style='margin-left:30px'> IOC  IOC </span><br/>
        <img style="margin-top:30px;" width="80%" src='./Spring3.png'/>
         <br/>
         <span style='margin-left:30px'>Spring  IOC  2 3 </span>
         <el-divider></el-divider>

        <br/><span style='margin-left:30px'><b></b> AOPAOP </span>
        <br/><br/><span style='margin-left:30px'>AOP  IOC  Java </span>
        <br/><br/><span style='margin-left:30px'> AOP  Java  Spring AOP  </span>
        <br/><br/><span style='margin-left:30px'>AOP  Spring </span>


        <div class='mark-block'>
            <el-button type="primary" icon="el-icon-edit" circle @click='clickNote(3)'></el-button>
            <el-button type="success" icon="el-icon-check" circle @click='clickKnow(3)'></el-button>
            <el-button type="warning" icon="el-icon-star-off" circle @click='clickUnknow(3)'></el-button>
        </div>

        <el-divider></el-divider>
        <!---------------------------------------------------------->
        <div class='sub-title forth-title'>
            IOC 
        </div>

        <span style='margin-left:30px'>Spring  org.springframework.beans  JavaBean  BeanFactory BeanFactory </span>
        <br /><br />
        <span style='margin-left:30px'>BeanFactory </span>

        <ul style='margin-left:30px;line-height:35px'>
            <li><b></b> Singleton </li>    
            <li><b></b> </li>
        </ul>

        <span style='margin-left:30px'>bean  Spring  IOC IOC Spring  JavaBean </span>

        <div class='mark-block'>
            <el-button type="primary" icon="el-icon-edit" circle @click='clickNote(4)'></el-button>
            <el-button type="success" icon="el-icon-check" circle @click='clickKnow(4)'></el-button>
            <el-button type="warning" icon="el-icon-star-off" circle @click='clickUnknow(4)'></el-button>
        </div>
        <!---------------------------------------------------------->
        <el-divider></el-divider>
        
        <div class='end-block'>
            
            <el-button type="danger" @click="dialogVisible = true" round></el-button>
            <el-button type="warning" @click="testVisible = true" round></el-button>
            <el-button type="success" @click="nextPage()" round></el-button>
        </div>
        <!---------------------------------------------------------->

        <el-dialog
            title="Chapter Reports"
            :visible.sync="dialogVisible"
            width="30%"
            >
            <div v-if='know.length!=0||unknow.length!=0'>
                <span v-if='know.length!=0'>
                <b>Already learned:</b> <span style='font-weight:bold;color:#67C23A;margin-right:1em' v-for="(k,index) in know" :key="index">{{k}} </span>
                </span>
                <br /><br />

                <span v-if='unknow.length!=0'>
                <b>Already highlighted:</b> <span style='font-weight:bold;color:#E6A23C;margin-right:1em' v-for="(u,index) in unknow" :key="index">{{u}} </span>
                </span>
            </div>

            <div v-if='know.length==0&&unknow.length==0'>
                <b>This chapter has not been marked yet! Start this chapter soon~</b>
            </div>

            <span slot="footer" class="dialog-footer">
                <el-button type="primary" @click="dialogVisible = false">Confirm</el-button>
            </span>
            </el-dialog>

            <!---------------------------------------------------------->
        <el-dialog
            title="Chapter Test"
            :visible.sync="testVisible"
            width="30%"
            :before-close="handleClose"
            >
            <el-card class="box-card">
                <div class="test-question">
                    <b>~</b>
                </div>

                <div class="test-confirm" style='text-align:right'>
                    <el-button type="primary" @click="testAnswer" >Confirm</el-button>
                </div>

            </el-card>

        </el-dialog>
    </div>

</template>



<script>
export default {
     data() {
      return {
        select:-1,
        dialogVisible: false,
        testVisible: false,
        know : [],
        unknow:[],
      };
    },

    methods:{
        clickKnow(value){

            switch(value){
                case 1:$('.first-title').css('background-color','#67C23A');this.knowPush("Spring ");break;
                case 2:$('.second-title').css('background-color','#67C23A');this.knowPush("Spring ");break;
                case 3:$('.third-title').css('background-color','#67C23A');this.knowPush("IOC  AOP");break;
                case 4:$('.forth-title').css('background-color','#67C23A');this.knowPush("IOC ");break;
            }

            this.know = Array.from(new Set(this.know))

            this.$notify({
                title: 'Success',
                message: 'You have learned this section',
                type: 'success'
            });
        },

        clickUnknow(value){

            switch(value){
                case 1:$('.first-title').css('background-color','#E6A23C');this.unknowPush("Spring ");break;
                case 2:$('.second-title').css('background-color','#E6A23C');this.unknowPush("Spring ");break;
                case 3:$('.third-title').css('background-color','#E6A23C');this.unknowPush("IOC  AOP");break;
                case 4:$('.forth-title').css('background-color','#E6A23C');this.unknowPush("IOC ");break;
            }

            this.unknow = Array.from(new Set(this.unknow))

            this.$notify({
                title: 'Highlight',
                message: 'You have highlighted this section',
                type: 'warning'
            });
        },

        knowPush(value){
            this.know.push(value)
            if(this.unknow.indexOf(value)!=-1){
                this.unknow.splice(this.unknow.indexOf(value),1);
            }
        },
        unknowPush(value){
            this.unknow.push(value)
            if(this.know.indexOf(value)!=-1){
                this.know.splice(this.know.indexOf(value),1);
            }
        },
        testAnswer(){
            if(this.select==3){
                this.$message({
                    message: `Congratulations. You're right!`,
                    type: 'success'
                });
            }else{
                 this.$message.error(`Oops! You're wrong.`);
            }
        },

        handleClose(){
            this.select = -1;
            this.testVisible = false;
        },
        clickNote(value){
            switch(value){
                case 1:this.$emit('note','Spring ');break;
                case 2:this.$emit('note','Spring ');break;
                case 3:this.$emit('note','IOC  AOP');break;
                case 4:this.$emit('note','IOC ');break;
            }
        },
        nextPage(){
            this.$router.push('/JavaFrame/MyBatis#header');
        }

    },
}
</script>


<style scoped>
    .SyntaxContent{
        text-align:left;
        line-height:30px;
    }

    .SyntaxContent h2{
        width:fit-content;
        display: inline-block;
        padding:2px 12px 4px;
        border-bottom: 4px #FF4F4F solid;
    }
    
    .sub-title{
        margin-top:5%;
        margin-bottom: 5%;
        padding-left:2em;
        padding-right:2em;
        font-weight: bold;
        font-size:20px;
        width:fit-content;
        background-color:#FAFAFA;
        height:2em;
        line-height:2em;
        border-radius:20px;
    }

    .code-block{
        color:white;
        margin-top:20px;
        margin-left:20px;
        margin-bottom: 20px;
        min-height:100px;
        width:80%;
        background-color: #292A2B;
        box-shadow: 0px 0px 10px 2px rgba(0, 0, 0, 0.2);
        border-radius: 15px;
        padding:16px;
        font-size:15px;
        line-height:1.5;
        font-family: Inter, system-ui, sans-serif;
    }

    .mark-block{
        width:80%;
        margin-top:50px;
        margin-left:20px;
        margin-bottom: 20px;
        text-align: end
    }
    .end-block{
        width:80%;
        margin-top:50px;
        margin-left:20px;
        margin-bottom: 100px;
        text-align: center
    }
</style>


