<template>

    <div class='SyntaxContent'>
        <h2>JSP </h2>
    
        <!---------------------------------------------------------->
        <div class='sub-title first-title'>
           Java Server Pages?
        </div>

        <span style='margin-left:30px'><b>JSPJava Server Pages</b>JSPHTMLJava<b>&lt;%%></b></span><br/>
        <br /><span style='margin-left:30px'>JSPJava servletJava webHTMLXHTMLXMLJSPJSP</span>
        <br /><br /><span style='margin-left:30px'>JSP</span>
        <br /><br /><span style='margin-left:30px'>JSPJavaBeans</span>

      

        <div class='mark-block'>
            <el-button type="primary" icon="el-icon-edit" circle @click='clickNote(1)'></el-button>
            <el-button type="success" icon="el-icon-check" circle @click='clickKnow(1)'></el-button>
            <el-button type="warning" icon="el-icon-star-off" circle @click='clickUnknow(1)'></el-button>
        </div>
        <el-divider></el-divider>
        <!---------------------------------------------------------->
        <div class='sub-title second-title'>
            JSP
        </div>

       <span style='margin-left:30px'>JSPCGICGIJSP</span><br/>
        <ul style='margin-left:30px;line-height:35px'>
            <li>JSPHTMLCGI</li>
            <li>JSPCGI/Perl</li>
            <li>JSP Java Servlet APIJSPJava APIJDBCJNDIEJBJAXP</li>
            <li>JSP Servlet Java servlet </li>
        </ul>
        <br/>
        <span style='margin-left:30px'>JSPJava EEJSP</span><br/>
        <div class='mark-block'>
            <el-button type="primary" icon="el-icon-edit" circle @click='clickNote(2)'></el-button>
            <el-button type="success" icon="el-icon-check" circle @click='clickKnow(2)'></el-button>
            <el-button type="warning" icon="el-icon-star-off" circle @click='clickUnknow(2)'></el-button>
        </div>

        <el-divider></el-divider>

        <!---------------------------------------------------------->
        <div class='sub-title third-title'>
            JSP
        </div>

        <span style='margin-left:30px'>JSP</span><br/>
        <ul style='margin-left:30px;line-height:35px'>
            <li><b>ASP</b>JSPJavaVBMSJSPMS</li>
            <li><b> Servlet </b>JSPHTMLprintln</li>
            <li><b>SSI</b>SSI</li>
            <li><b>JavaScript</b>JavaScriptHTML</li>
            <li><b>HTML</b>HTML</li>
        </ul>
        <br/>
        <div class='mark-block'>
            <el-button type="primary" icon="el-icon-edit" circle @click='clickNote(3)'></el-button>
            <el-button type="success" icon="el-icon-check" circle @click='clickKnow(3)'></el-button>
            <el-button type="warning" icon="el-icon-star-off" circle @click='clickUnknow(3)'></el-button>
        </div>


        <!---------------------------------------------------------->


        <!---------------------------------------------------------->
        <el-divider></el-divider>
        
        <div class='end-block'>
            
            <el-button type="danger" @click="dialogVisible = true" round></el-button>
            <el-button type="warning" @click="testVisible = true" round></el-button>
            <el-button type="success" @click="nextPage()" round></el-button>
        </div>
        <!---------------------------------------------------------->

        <el-dialog
            title="Chapter Reports"
            :visible.sync="dialogVisible"
            width="30%"
            >
            <div v-if='know.length!=0||unknow.length!=0'>
                <span v-if='know.length!=0'>
                <b>Already learned:</b> <span style='font-weight:bold;color:#67C23A;margin-right:1em' v-for="(k,index) in know" :key="index">{{k}} </span>
                </span>
                <br /><br />

                <span v-if='unknow.length!=0'>
                <b>Already highlighted:</b> <span style='font-weight:bold;color:#E6A23C;margin-right:1em' v-for="(u,index) in unknow" :key="index">{{u}} </span>
                </span>
            </div>

            <div v-if='know.length==0&&unknow.length==0'>
                <b>This chapter has not been marked yet! Start this chapter soon~</b>
            </div>

            <span slot="footer" class="dialog-footer">
                <el-button type="primary" @click="dialogVisible = false">Confirm</el-button>
            </span>
            </el-dialog>

            <!---------------------------------------------------------->
        <el-dialog
            title="Chapter Test"
            :visible.sync="testVisible"
            width="30%"
            :before-close="handleClose"
            >
            <el-card class="box-card">
                <div class="test-question">
                    <b>Q: JSP</b>
                </div>
                
                <div class="test-select" style='margin:20px 0'>
                    <el-radio v-model="select" label="1" style='display:block;line-height:36px;'>&lt;! out.println("hello") !></el-radio>
                    <el-radio v-model="select" label="2" style='display:block;line-height:36px;'>&lt;/ out.println("hello") /></el-radio>
                    <el-radio v-model="select" label="3" style='display:block;line-height:36px;'>&lt;% out.println("hello") %></el-radio>
                    <el-radio v-model="select" label="4" style='display:block;line-height:36px;'>&lt;* out.println("hello") *></el-radio>
                </div>

                <div class="test-confirm" style='text-align:right'>
                    <el-button type="primary" @click="testAnswer" >Confirm</el-button>
                </div>

            </el-card>

        </el-dialog>
    </div>

</template>



<script>
export default {
     data() {
      return {
        select:-1,
        dialogVisible: false,
        testVisible: false,
        know : [],
        unknow:[],
      };
    },

    methods:{
        clickKnow(value){

            switch(value){
                case 1:$('.first-title').css('background-color','#67C23A');this.knowPush("Java Server Pages? ");break;
                case 2:$('.second-title').css('background-color','#67C23A');this.knowPush("JSP");break;
                case 3:$('.third-title').css('background-color','#67C23A');this.knowPush("JSP");break;
            }

            this.know = Array.from(new Set(this.know))

            this.$notify({
                title: 'Success',
                message: 'You have learned this section',
                type: 'success'
            });
        },

        clickUnknow(value){

            switch(value){
                case 1:$('.first-title').css('background-color','#E6A23C');this.unknowPush("Java Server Pages? ");break;
                case 2:$('.second-title').css('background-color','#E6A23C');this.unknowPush("JSP");break;
                case 3:$('.third-title').css('background-color','#E6A23C');this.unknowPush("JSP");break;
            }

            this.unknow = Array.from(new Set(this.unknow))

            this.$notify({
                title: 'Highlight',
                message: 'You have highlighted this section',
                type: 'warning'
            });
        },

        knowPush(value){
            this.know.push(value)
            if(this.unknow.indexOf(value)!=-1){
                this.unknow.splice(this.unknow.indexOf(value),1);
            }
        },
        unknowPush(value){
            this.unknow.push(value)
            if(this.know.indexOf(value)!=-1){
                this.know.splice(this.know.indexOf(value),1);
            }
        },
        testAnswer(){
            if(this.select==3){
                this.$message({
                    message: `Congratulations. You're right!`,
                    type: 'success'
                });
            }else{
                 this.$message.error(`Oops! You're wrong.`);
            }
        },

        handleClose(){
            this.select = -1;
            this.testVisible = false;
        },
        clickNote(value){
            switch(value){
                case 1:this.$emit('note','Java Server Pages? ');break;
                case 2:this.$emit('note','JSP');break;
                case 3:this.$emit('note','JSP');break;
            }
        },
        nextPage(){
            this.$router.push('/JavaWeb/JSP-structure#header');
        }

    },
}
</script>


<style scoped>
    .SyntaxContent{
        text-align:left;
        line-height:30px;
    }

    .SyntaxContent h2{
        width:fit-content;
        display: inline-block;
        padding:2px 12px 4px;
        border-bottom: 4px #FF4F4F solid;
    }
    
    .sub-title{
        margin-top:5%;
        margin-bottom: 5%;
        padding-left:2em;
        padding-right:2em;
        font-weight: bold;
        font-size:20px;
        width:fit-content;
        background-color:#FAFAFA;
        height:2em;
        line-height:2em;
        border-radius:20px;
    }

    .code-block{
        color:white;
        margin-top:20px;
        margin-left:20px;
        margin-bottom: 20px;
        min-height:100px;
        width:80%;
        background-color: #292A2B;
        box-shadow: 0px 0px 10px 2px rgba(0, 0, 0, 0.2);
        border-radius: 15px;
        padding:16px;
        font-size:15px;
        line-height:1.5;
        font-family: Inter, system-ui, sans-serif;
    }

    .mark-block{
        width:80%;
        margin-top:50px;
        margin-left:20px;
        margin-bottom: 20px;
        text-align: end
    }
    .end-block{
        width:80%;
        margin-top:50px;
        margin-left:20px;
        margin-bottom: 100px;
        text-align: center
    }
</style>


