<template>

    <div class='SyntaxContent'>
        <h2>JSP </h2>
    
        <!---------------------------------------------------------->
        <div class='sub-title first-title'>
            JSP 
        </div>

        <span style='margin-left:30px'> JSP  JSP  JSP  JSP  Apache  JSP </span><br/>
        <br/><span style='margin-left:30px'>JSP  Web JSP JSP  JSP  JSP  Web </span>
        <br/><img style="margin-top:30px;" width="80%" src='./JSP1.png'/>


        <div class='mark-block'>
            <el-button type="primary" icon="el-icon-edit" circle @click='clickNote(1)'></el-button>
            <el-button type="success" icon="el-icon-check" circle @click='clickKnow(1)'></el-button>
            <el-button type="warning" icon="el-icon-star-off" circle @click='clickUnknow(1)'></el-button>
        </div>
        <el-divider></el-divider>


        <!---------------------------------------------------------->
        <div class='sub-title second-title'>
            JSP 
        </div>

        <span style='margin-left:30px'> Web JSP</span><br/>
        
        <ul style='margin-left:30px;line-height:35px'>
            <li> HTTP </li>    
            <li>Web  JSP  JSP  URL .jsp </li>
            <li>JSP  JSP  Servlet println()  JSP  Java </li>
            <li>JSP  Servlet  Servlet </li>
            <li>Web  Servlet  Servlet Servlet  HTML  HTTP response  Web </li>
            <li>Web  HTML  HTTP response </li>
            <li>Web  HTTP response HTML</li>
        </ul>

        <span style='margin-left:30px'></span><br/>
        <br/><img style="margin-top:30px;" width="80%" src='./JSP2.png'/>

        <br/><br/><span style='margin-left:30px'>JSP  JSP  Servlet  JSP  Servlet JSP  Servlet JSP  Servlet  PHP</span>
        <br/><br/><span style='margin-left:30px'>JSP  Servlet  Java JSP  Servlet </span>
        <div class='mark-block'>
            <el-button type="primary" icon="el-icon-edit" circle @click='clickNote(2)'></el-button>
            <el-button type="success" icon="el-icon-check" circle @click='clickKnow(2)'></el-button>
            <el-button type="warning" icon="el-icon-star-off" circle @click='clickUnknow(2)'></el-button>
        </div>

        <!---------------------------------------------------------->
        
        <!---------------------------------------------------------->
        <el-divider></el-divider>
        
        <div class='end-block'>
            
            <el-button type="danger" @click="dialogVisible = true" round></el-button>
            <el-button type="warning" @click="testVisible = true" round></el-button>
            <el-button type="success" @click="nextPage()" round></el-button>
        </div>
        <!---------------------------------------------------------->

        <el-dialog
            title="Chapter Reports"
            :visible.sync="dialogVisible"
            width="30%"
            >
            <div v-if='know.length!=0||unknow.length!=0'>
                <span v-if='know.length!=0'>
                <b>Already learned:</b> <span style='font-weight:bold;color:#67C23A;margin-right:1em' v-for="(k,index) in know" :key="index">{{k}} </span>
                </span>
                <br /><br />

                <span v-if='unknow.length!=0'>
                <b>Already highlighted:</b> <span style='font-weight:bold;color:#E6A23C;margin-right:1em' v-for="(u,index) in unknow" :key="index">{{u}} </span>
                </span>
            </div>

            <div v-if='know.length==0&&unknow.length==0'>
                <b>This chapter has not been marked yet! Start this chapter soon~</b>
            </div>

            <span slot="footer" class="dialog-footer">
                <el-button type="primary" @click="dialogVisible = false">Confirm</el-button>
            </span>
            </el-dialog>

            <!---------------------------------------------------------->
        <el-dialog
            title="Chapter Test"
            :visible.sync="testVisible"
            width="30%"
            :before-close="handleClose"
            >
            <el-card class="box-card">
                <div class="test-question">
                    <b>~</b>
                </div>
                

            </el-card>

        </el-dialog>
    </div>

</template>



<script>
export default {
     data() {
      return {
        select:-1,
        dialogVisible: false,
        testVisible: false,
        know : [],
        unknow:[],
      };
    },

    methods:{
        clickKnow(value){

            switch(value){
                case 1:$('.first-title').css('background-color','#67C23A');this.knowPush("JSP ");break;
                case 2:$('.second-title').css('background-color','#67C23A');this.knowPush("JSP ");break;
                case 3:$('.third-title').css('background-color','#67C23A');this.knowPush("IOC  AOP");break;
                case 4:$('.forth-title').css('background-color','#67C23A');this.knowPush("IOC ");break;
            }

            this.know = Array.from(new Set(this.know))

            this.$notify({
                title: 'Success',
                message: 'You have learned this section',
                type: 'success'
            });
        },

        clickUnknow(value){

            switch(value){
                case 1:$('.first-title').css('background-color','#E6A23C');this.unknowPush("JSP ");break;
                case 2:$('.second-title').css('background-color','#E6A23C');this.unknowPush("JSP ");break;
                case 3:$('.third-title').css('background-color','#E6A23C');this.unknowPush("IOC  AOP");break;
                case 4:$('.forth-title').css('background-color','#E6A23C');this.unknowPush("IOC ");break;
            }

            this.unknow = Array.from(new Set(this.unknow))

            this.$notify({
                title: 'Highlight',
                message: 'You have highlighted this section',
                type: 'warning'
            });
        },

        knowPush(value){
            this.know.push(value)
            if(this.unknow.indexOf(value)!=-1){
                this.unknow.splice(this.unknow.indexOf(value),1);
            }
        },
        unknowPush(value){
            this.unknow.push(value)
            if(this.know.indexOf(value)!=-1){
                this.know.splice(this.know.indexOf(value),1);
            }
        },
        testAnswer(){
            if(this.select==3){
                this.$message({
                    message: `Congratulations. You're right!`,
                    type: 'success'
                });
            }else{
                 this.$message.error(`Oops! You're wrong.`);
            }
        },

        handleClose(){
            this.select = -1;
            this.testVisible = false;
        },
        clickNote(value){
            switch(value){
                case 1:this.$emit('note','JSP ');break;
                case 2:this.$emit('note','JSP ');break;
                case 3:this.$emit('note','IOC  AOP');break;
                case 4:this.$emit('note','IOC ');break;
            }
        },
        nextPage(){
            this.$router.push('/JavaFrame/Spring#header');
        }

    },
}
</script>


<style scoped>
    .SyntaxContent{
        text-align:left;
        line-height:30px;
    }

    .SyntaxContent h2{
        width:fit-content;
        display: inline-block;
        padding:2px 12px 4px;
        border-bottom: 4px #FF4F4F solid;
    }
    
    .sub-title{
        margin-top:5%;
        margin-bottom: 5%;
        padding-left:2em;
        padding-right:2em;
        font-weight: bold;
        font-size:20px;
        width:fit-content;
        background-color:#FAFAFA;
        height:2em;
        line-height:2em;
        border-radius:20px;
    }

    .code-block{
        color:white;
        margin-top:20px;
        margin-left:20px;
        margin-bottom: 20px;
        min-height:100px;
        width:80%;
        background-color: #292A2B;
        box-shadow: 0px 0px 10px 2px rgba(0, 0, 0, 0.2);
        border-radius: 15px;
        padding:16px;
        font-size:15px;
        line-height:1.5;
        font-family: Inter, system-ui, sans-serif;
    }

    .mark-block{
        width:80%;
        margin-top:50px;
        margin-left:20px;
        margin-bottom: 20px;
        text-align: end
    }
    .end-block{
        width:80%;
        margin-top:50px;
        margin-left:20px;
        margin-bottom: 100px;
        text-align: center
    }
</style>


